import { DragDropContext, Droppable, DropResult } from "react-beautiful-dnd";
import { useRecoilState } from "recoil";
import styled from "styled-components";
import { boardState, toDoState } from "./atoms";
import Board from "./Components/Board";
import DeleteCard from "./Components/DeleteCard";
import Header from "./Header";

const Wrapper = styled.div`
  display: flex;
  max-width: 680px;
  width: 100%;
  margin: 0 auto;
  justify-content: center;
  align-items: center;
  height: 100vh;
`;

const Boards = styled.div`
  display: grid;
  gap: 10px;
  justify-content: center;
  grid-template-columns: repeat(3, 1fr);
`;

function DragnDrop() {
  const [toDos, setToDos] = useRecoilState(toDoState);
  const [boards, setBoards] = useRecoilState(boardState);

  //     onDragEnd
  const onDragEnd = (info: DropResult) => {
    const { destination, draggableId, source } = info;

    if (!destination) return;

    //  
    if (source.droppableId === "boards") {
      setToDos((allBoard) => {
        const copiedBoard = Object.entries({ ...allBoard });
        const cutTodo = [...copiedBoard.splice(source.index, 1)];
        copiedBoard.splice(destination.index, 0, ...cutTodo);
        return {
          ...Object.fromEntries(copiedBoard),
        };
        // const copy = [...allBoard];
        // copy.splice(source.index, 1);
        // copy.splice(destination?.index, 0, draggableId);
        // return copy;
      });
    }

    //  
    if (destination.droppableId === "delete") {
      setToDos((allBoards) => {
        //  board 
        const deleteCopy = [...allBoards[source.droppableId]];
        // board    
        deleteCopy.splice(source.index, 1);
        return {
          ...allBoards,
          [source.droppableId]: deleteCopy,
        };
      });
    }

    //   
    if (destination?.droppableId === source.droppableId) {
      setToDos((allBoards) => {
        const boardCopy = [...allBoards[source.droppableId]];
        const taskObj = boardCopy[source.index];
        // 1)     
        boardCopy.splice(source.index, 1); // source.index    item  
        // 2)      
        boardCopy.splice(destination?.index, 0, taskObj); // destination?.index   index
        //        
        return {
          ...allBoards,
          [source.droppableId]: boardCopy,
        };
      });
    }

    //   
    if (destination.droppableId !== source.droppableId) {
      setToDos((allBoards) => {
        const sourceBoard = [...allBoards[source.droppableId]]; //   id
        const taskObj = sourceBoard[source.index];
        const destinationBoard = [...allBoards[destination.droppableId]]; //    id
        sourceBoard.splice(source.index, 1);
        destinationBoard.splice(destination.index, 0, taskObj);
        return {
          ...allBoards,
          [source.droppableId]: sourceBoard,
          [destination.droppableId]: destinationBoard,
        };
      });
    }
  };

  return (
    <DragDropContext onDragEnd={onDragEnd}>
      <Header />
      <Wrapper>
        <Droppable droppableId="boards" direction="horizontal" type="board">
          {(magic, snapshot) => (
            <Boards ref={magic.innerRef} {...magic.droppableProps}>
              {Object.keys(toDos).map((boardId, index) => (
                <Board
                  toDos={toDos[boardId]}
                  boardId={boardId}
                  key={boardId}
                  index={index}
                />
              ))}
              {magic.placeholder}
            </Boards>
          )}
        </Droppable>
        <DeleteCard />
      </Wrapper>
    </DragDropContext>
  );
}
export default DragnDrop;
